import './App.css'
import React, {useState} from 'react'
import CustomerService from './services/Sanat'
import axios from "axios";

const Sanat = () => {
    const [data, setData] = useState({
      body:""
    });
    const [customers, setCustomers] = useState([])
    const handleChange = (e) => {
      const value = e.target.value;
      setData({
        ...data,
        [e.target.name]: value
      });
    };
 
    const handleSubmit = (e) => {
      e.preventDefault();
      const userData = {
        body: data.body
        
      };
      
      const config = {
        headers: { 'apikey': process.env.REACT_APP_RAPID_API_KEY},
    }
      axios
        .post("https://api.apilayer.com/keyword", userData, config)
        .then((response) => {
          console.log(response.data.result);
          setCustomers(response.data.result)
        })
        .catch((error) => {
          if (error.response) {
            console.log(error.response);
            console.log("server responded");
            // console.log(data.body)
            // setCustomers(data)
          } else if (error.request) {
            console.log("network error");
          } else {
            console.log(error);
          }
        });
        
    };
  
    return (
 

<div className="w3-container" id="where" style={{paddingBottom:32}}>
  <div className="w3-content" style={{ maxWidth: 700 }}>
    <h5 className="w3-center w3-padding-48"><span className="w3-tag w3-wide">ETSI AVAINSANAT</span></h5>
    {/* <p>Find us at some address at some place.</p> */}
    {/* */}
    <p><span className="w3-tag"> <img src={require('./assets/img/about.jpg')} className="w3-image" style={{height:50, margin:0 }}/></span> Tässä osiossa pystyy hakemaan tekstistä avainsanoja. Sanoja pystyy hakemaan myös suomen kielisistä, mutta tulos ei ole niin hyvä kuin englanninkielisissä teksteissä. Haasteena tässä oli piilottaa API avaimen. Sitä varten tein pienen node.js backendin, johon laitoin API avaimen.</p>
    {/* <p><strong>Reserve</strong> a table, ask for today's special or just send us a message:</p> */}
    <form onSubmit={handleSubmit}>

      <p><input className="w3-input w3-padding-16 w3-border" type="text" placeholder="Tekstisi" required name="body" value={data.body}
              onChange={handleChange}/></p>
      <p><button className="w3-button w3-black" type="submit">HAE AVAINSANAT</button></p>
    </form>
   <table class="table">
        <thead>
          <tr>
            <th scope="col">#</th>
            <th scope="col">Sanat</th>
            <th scope="col">Osuvuus</th>
            
          </tr>
        </thead>
        <tbody>
  {customers.map((e, i)=>{
       return (
      
          <tr key={e.score}>
            <th scope="row">{i}</th>
            <td>{e.text}</td>
            <td>{e.score}</td>
            
          </tr>
          
      
     );})}</tbody>
      </table>
   </div>
</div>
         
    );
  };

export default Sanat;